@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@500;700&display=swap);
* {
  margin: 0;
  box-sizing: border-box;
}

#biggerThanTablet {
  height: 100vh;
  background-color: #384862;
  padding: 0px;
  border: none;
}

.updateItemDate{
    font-weight: bold;
    border-right: 1px solid red;
}
.newNoticeAlert {
  -webkit-animation: newNotice 1s ease;
          animation: newNotice 1s ease;
  -webkit-animation-iteration-count: 10;
          animation-iteration-count: 10;
}

@-webkit-keyframes newNotice {
  from {
    color: #34495e;
  }

  to {
    color: white;
  }
}

@keyframes newNotice {
  from {
    color: #34495e;
  }

  to {
    color: white;
  }
}

#navTitle {
  margin-right: 100px;
  margin-left: 50px;
  color: white;
  cursor: pointer;
}

#navTitleMobile {
  color: white;
  cursor: pointer;
}

.item .headerItem {
  color: white;
  padding: 0 25px;
  cursor: pointer;
}

.personIconItem {
  margin-left: auto;
  margin-right: 50px;
}

.item .personIcon {
  color: white;
  padding: 0 50px;
  display: flex;
  cursor: pointer;
}

.fullPage {
  color: white;
  /* height: 93vh; */
  background-color: #384862;
  margin: 0 !important;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 500;
  padding-bottom: 20px !important;
}

#gridSegment {
  padding: 0px;
  background-color: #384862;
  border: none;
  height: 94vh;
  margin-top: 0px;
}

#gridContainer {
  width: 95%;
  margin-left: 2.5%;
  margin-right: 2.5%;
  border: none;
}

.headerValue > .header {
  color: white;
}

.contentHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.contentColumn {
  width: 150px;
}

div.pagination {
  border-bottom: none !important;
}

div.pagination a {
  color: white !important;
}

div.pagination a.active {
  color: #ff695e !important;
  border-bottom-width: 2px !important;
  border-bottom-color: #ff695e !important;
}

div.eachRow {
  padding-top: 11px !important;
  padding-bottom: 11px !important;
}

div.eachRow:hover,
.abrelRow:hover {
  background-color: #148f77;
}

.contentImage {
  height: 24px !important;
  width: 24px !important;
}

.iconClass {
  cursor: pointer;
}

div#clickPopup {
  padding: 8px;
  border: none;
}

